import React from "react";
// Import Swiper React components
import { Swiper, SwiperSlide } from "swiper/react";

import { HiArrowNarrowRight } from 'react-icons/hi';
// Import Swiper styles
import "swiper/css";
import "swiper/css/navigation";

// import required modules
import { Navigation } from "swiper";
import { Link } from "gatsby";



export default function Portdolio() {
  return (
    <div id='works' className='w-full h-screen bg-[#0a192f] text-gray-300'>
      <div className='max-w-[1000px] mx-auto px-8 flex flex-col justify-center h-full'>
        <div className=' pb-8 pl-4'>
            <p className='text-4xl font-bold inline border-b-4 border-pink-600'>
              My Works
            </p>
          </div>
        <Swiper navigation={true} modules={[Navigation]} className="w-[100%] h-[300px]">
          <SwiperSlide  className="flex justify-center items-center">
            <div className="w-[80%]">
              <img src="/logo.svg" alt="audiophile"/>
              <p className="my-[10px]">I enjoy building things for the web . My interest in web development started back in 2019 so I decided to study computer science and learn programming in general</p>
              <Link to="#works">
                <button className='text-white group border-2 px-6 py-3 my-4 flex items-center   hover:bg-pink-600  hover:border-pink-600'>Demo
                  <span className='group-hover:ml-3 duration-300'>
                    <HiArrowNarrowRight className='ml-3 ' />
                  </span>
                </button >
              </Link>
            </div>
          </SwiperSlide>
          <SwiperSlide  className="flex justify-center items-center">
            <div className="w-[80%]">
              <img src="/invoices.png" alt="invoices" className="w-[50px] h-[50px]" />
              <p className="my-[10px]">I enjoy building things for the web . My interest in web development started back in 2019 so I decided to study computer science and learn programming in general</p>
              <Link to="#works">
                <button className='text-white group border-2 px-6 py-3 my-4 flex items-center   hover:bg-pink-600  hover:border-pink-600'>Demo
                  <span className='group-hover:ml-3 duration-300'>
                    <HiArrowNarrowRight className='ml-3 ' />
                  </span>
                </button >
              </Link>
            </div>
          </SwiperSlide>
        </Swiper>
      </div>
      
    </div>
  );
}